import React, { Component } from 'react';
export default class References extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="testimonials">
                    <div className="text-container">
                        <div className="row">
                            <div className="two columns header-col">
                                <h1><span>References</span></h1>
                            </div>
                            <div className="ten columns flex-container">
                                <div className="flexslider">
                                    <ul className="slides">
                                        <li>
                                            <blockquote>
                                                <p>Ben was an excellent addition to the team and made a great impact in the short time
                                                  he was with us. I was fortunate to work on a couple projects with Ben and found
                                                  him to be a great listener, learner and executor. He also produced a high volume
                                                  of work while maintaining standards for quality.
                                                </p>
                                                <a href="https://www.linkedin.com/in/matt-james-b373b12/" /><cite>Matt James (Convercent)</cite>
                                            </blockquote>
                                        </li> {/* slide ends */}
                                    </ul>
                                </div> {/* div.flexslider ends */}
                            </div> {/* div.flex-container ends */}
                        </div> {/* row ends */}
                    </div>  {/* text-container ends */}
                </section>

            </React.Fragment>
        );
    }
}