import React, { Component } from 'react';
export default class Works extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="portfolio">
                    <div className="row">
                        <div className="twelve columns collapsed">
                            <h1>Check out some of the programming projects I've worked on and some of the music I've made!</h1>
                            {/* portfolio-wrapper */}
                            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-01" title>
                                            <img alt="" src="images/portfolio/h4h8.jpg" />
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Hack for Humanity 2018</h5>
                                                    <p>Hackathon</p>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus" /></div>
                                        </a>
                                    </div>
                                </div> {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-02" title>
                                            <img alt="" src="images/portfolio/leasr.png" />
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Leasr</h5>
                                                    <p>Personal Project</p>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus" /></div>
                                        </a>
                                    </div>
                                </div> {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-03" title>
                                            <img alt="" src="images/portfolio/stargazing.png" />
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Travis Scott- Stargazing Remix</h5>
                                                    <p>Music</p>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus" /></div>
                                        </a>
                                    </div>
                                </div> {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-04" title>
                                            <img alt="" src="images/portfolio/sunset.png" />
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Petit Biscuit x Drake Mashup</h5>
                                                    <p>Music</p>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus" /></div>
                                        </a>
                                    </div>
                                </div> {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-05" title>
                                            <img alt="" src="images/portfolio/ganz.png" />
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Ganz x Flume x Kanye West Mashup</h5>
                                                    <p>Music</p>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus" /></div>
                                        </a>
                                    </div>
                                </div> {/* item end */}
                            </div> {/* portfolio-wrapper end */}
                        </div> {/* twelve columns end */}
                        {/* Modal Popup
	      --------------------------------------------------------------- */}
                        <div id="modal-01" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/h4h8.jpg" alt="" />
                            <div className="description-box">
                                <h4>Hack for Humanity 2018</h4>
                                <p>During Santa Clara's annual hackathon, in a team of six, we designed an android app to help households in need find supplies in the event
                                    of a natural disaster. The app was created with the Google Maps API and Android Studio.
                                </p>
                                
                            </div>
                            <div className="link-box">
                                <a href="https://github.com/mitchahan/Hackathon">Github</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>{/* modal-01 End */}
                        <div id="modal-02" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/leasr.png" alt="" />
                            <div className="description-box">
                                <h4>Leasr</h4>
                                <p>A personal project I'm working on with a few peers that will allow Santa Clara students to crowd source student housing and sublets. 
                                    We are developing this web application with ReactJS and the Google Maps API.
                                </p>
                                <span className="categories"><i className="fa fa-tag" />Web Development</span>
                            </div>
                            <div className="link-box">
                                <a href="https://github.com/bdreyerr/leasr">Github(Private until finished)</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>{/* modal-02 End */}
                        <div id="modal-03" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/stargazing.png" alt="" />
                            <div className="description-box">
                                <h4>Travis Scott - Stargazing Remix</h4>
                                <p>Travis Scott is one my personal favorites, so when I heard Stargazing for the first time, I just knew I had to put my own spin on it. Click the link below to listen!</p>
                                <span className="categories"><i className="fa fa-tag" />Music</span>
                            </div>
                            <div className="link-box">
                                <a href="https://soundcloud.com/southxx/travis-scott-stargazing-south-remix">Listen on SoundCloud</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>{/* modal-03 End */}
                        <div id="modal-04" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/sunset.png" alt="" />
                            <div className="description-box">
                                <h4>Petit Biscuit x Drake Mashup</h4>
                                <p>Three of my favorite songs mashed up into one beautiful track!</p>
                                <span className="categories"><i className="fa fa-tag" />Music</span>
                            </div>
                            <div className="link-box">
                                <a href="https://soundcloud.com/southxx/petite-biscuit-x-denzel-curry-x-drake-sunset-lover">Listen on SoundCloud</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>{/* modal-04 End */}
                        <div id="modal-05" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/ganz.png" alt="" />
                            <div className="description-box">
                                <h4>Ganz x Flume x Kanye West Mashup</h4>
                                <p>Another mashup of some amazing songs.</p>
                                <span className="categories"><i className="fa fa-tag" />Music</span>
                            </div>
                            <div className="link-box">
                                <a href="https://soundcloud.com/southremix/flume-x-ganz-x-kanye-west-x-yo-gotti-x-ti-hyperparadise">Listen on SoundCloud</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>{/* modal-05 End */}
                    </div> {/* row End */}
                </section>
            </React.Fragment>
        );
    }
}