import React, { Component } from 'react';
export default class Contact extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="contact">
                    <div className="row section-head">
                        <div className="two columns header-col">
                            <h1><span>Get In Touch.</span></h1>
                        </div>
                        <div className="ten columns">
                            <p className="lead">Reach out to me if you have any questions or queries. Email is the easiest way to get in contact, but feel free to give me a call as well!</p>
                            <a href="mailto:bdreyer@scu.edu">bdreyer@scu.edu</a><br />
                            <span>US: +1-303-656-6110</span><br />
                            <span>Singapore: +65-8296-7338</span>
                        </div>
                    </div>
                </section>

            </React.Fragment>
        );
    }
}