import React, { Component } from 'react';
export default class About extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="about">
                    <div className="row">
                        <div className="three columns">
                            <img className="profile-pic" src="images/ben.jpg" alt="" />
                        </div>
                        <div className="nine columns main-col">
                            <h2>About Me</h2>
                            <p>I'm currently in my third year at Santa Clara University studying Computer Science and Mathematics. While Living 
                                in California is a dream come true, I miss my beautiful home state of Colorado every day. You can find me in Singapore
                                until December of 2019 studying abroad at Nanyang Technological
                                University, one of Asia's most prestigious universities. I plan to declare my emphasis in software engineering, but I also
                                have strong passions for web development. This website was created by me using React.js.
                            </p>
                            <div className="row">
                            </div> {/* end row */}
                        </div> {/* end .main-col */}
                    </div>
                </section>
            </React.Fragment>
        );
    }
}